import React, { Component } from 'react'
import * as ApiProvider from '../../API/ApiProvider'
import { Spin, Icon } from 'antd';
import moment from 'moment'

class Chronictablecelltooltip extends Component {
    state = {

    }
    componentDidUpdate() {

    }
    componentDidMount() {

    }
    callBack = (event) => {
        const detailMo = {
            'month': event.target.getAttribute('ORMM'),
            'seq': event.target.getAttribute('ORDSEQNO'),
            'HID': event.target.getAttribute('HID'),
            'CID': event.target.getAttribute('CID'),
        }
        this.props.parentDateCallback(detailMo);
    }
    ReportLink() {
        const { tooltipelement, caseinfo } = this.props
        const ORMM = tooltipelement.ORMM.toString().trim()
        const ORDSEQNO = tooltipelement.ORDSEQNO.toString().trim()
        const HID = caseinfo.HISID.toString().trim()
        const CID = caseinfo.CASEID.toString().trim()

        return (
            <div HID={HID} CID={CID} ORMM={ORMM} ORDSEQNO={ORDSEQNO} style={{ textDecoration: "underline", cursor: "pointer" }} onClick={this.callBack}>{"顯示"}</div>
        );
    }
    render() {
        const { tooltipelement } = this.props
        console.log(this.props)
        const infoTime = tooltipelement.ORRCPTM.toString().trim()
        const name = tooltipelement.ORPROCED.toString().trim()
        const reportname = tooltipelement.ORSPCN1.toString().trim()
        const ReportLink = this.ReportLink()
        const cellRenderData = [infoTime, name, reportname, ReportLink]
        const Cell = cellRenderData.map(function (value, index) {
            return (
                <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "0px 5px" }}>
                    {value}
                </div>
            );
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "1fr 2fr 1fr 1fr" }}>{Cell}</div>
        );
    }
}

export default Chronictablecelltooltip
