    apiGethub() {
        ApiGithub.getGitHubJson("Labview.json")
            .then(result => {
                console.log(result.data);
                this.setState({ parser: result.data })
            })
            .catch(err => {
                console.log("getGitHubJson Labview.json NG " + JSON.stringify(err));
            })
    }
    APIUpdate(DateRange, HID) {
        this.setState({ isLoading: true });
        function selectMonthInterval(PickerDate) {
            const timestampInterval = new Date().getTime() - new Date(PickerDate).getTime()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }

        const st = moment(DateRange, "YYYYMMDD").format("YYYY-MM-DD")
        console.log(DateRange, HID)
        const datePick = moment(DateRange, "YYYYMMDD").format('YYYY/MM/DD')
        const MonthInterval = selectMonthInterval(datePick)
        const RESDGLU1 = ApiProvider.getStoredProcedureRESDGLU1(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESDGLU1 NG " + JSON.stringify(err));
            });
        const RESLAB02 = ApiProvider.getStoredProcedureRESLAB02(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESLAB02 NG " + JSON.stringify(err));
            });
        const RESLAB01 = ApiProvider.getStoredProcedureRESLAB01(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESLAB01 NG " + JSON.stringify(err));
            });
        const RESDBGAS = ApiProvider.getStoredProcedureRESDBGAS(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESDBGAS NG " + JSON.stringify(err));
            });
        const RESLAB0C = ApiProvider.getStoredProcedureRESLAB0C(HID, st)
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESLAB0C NG " + JSON.stringify(err));
            });

        const loginAcc = sessionStorage.getItem('user');
        const ORDLIST = ApiProvider.getStoredProcedureORDLIST(HID, st, "ALL", loginAcc)
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const RESSECT = ApiProvider.getStoredProcedureRESSECT(HID, MonthInterval, "ALL", loginAcc)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESSECT NG " + JSON.stringify(err))
            });
        const Ca_RESDISP = ApiProvider.getStoredProcedureRESDISP_parse(HID, MonthInterval, "ALL", loginAcc, "Labview value Ca++")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESDISP_parse NG " + JSON.stringify(err))
            });
        const Mg_RESDISP = ApiProvider.getStoredProcedureRESDISP_parse(HID, MonthInterval, "ALL", loginAcc, "Labview value Mg++")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESDISP_parse NG " + JSON.stringify(err))
            });
        const Pct_RESDISP = ApiProvider.getStoredProcedureRESDISP_parse(HID, MonthInterval, "ALL", loginAcc, "Labview value Procalcitonin")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESDISP_parse NG " + JSON.stringify(err))
            });
        const BNP_RESDISP = ApiProvider.getStoredProcedureRESDISP_parse(HID, MonthInterval, "ALL", loginAcc, "Labview value BNP")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESDISP_parse NG " + JSON.stringify(err))
            });
        Promise.all([RESDGLU1, RESLAB02, RESLAB01, RESDBGAS, RESLAB0C, ORDLIST, RESSECT, Ca_RESDISP, Mg_RESDISP, Pct_RESDISP, BNP_RESDISP]).then(result => {
            this.setState({
                RESDGLU1: result[0], //累積報告
                RESLAB02: result[1],  //累積報告
                RESLAB01: result[2],  //累積報告
                RESDBGAS: result[3],  //累積報告
                RESLAB0C: result[4],  //累積報告
                ORDLIST: result[5],
                RESSECT: result[6],
                Ca_RESDISP: result[7],
                Mg_RESDISP: result[8],
                Pct_RESDISP: result[9],
                Bnp_RESDISP: result[10],
                isLoading: false,
            })
        }).catch(() => { this.setState({ isLoading: false }); });
    }
