import React, { Component } from 'react';
import { Icon, Modal, Input, Select } from 'antd'


export default class PowerSettingCell extends Component {
    state = {
        editMode: false,
        deleteMode: false,
        role: 0
    }
    constructor(props) {
        super();
        this.adduserhandleChange = this.adduserhandleChange.bind(this)
    }

    componentDidMount() {
        const { cellData } = this.props
        this.setState({ role: cellData.role })
    }
    showModal = () => {
        this.setState({
            deleteMode: true,
        });
    };

    handleOk = e => {
        const { cellData } = this.props
        this.props.callback(cellData, "delete")
        this.setState({
            deleteMode: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            deleteMode: false,
        });
    };

    onSave = () => {
        const { cellData } = this.props
        console.log(this.state.role)
        const data = {
            doctorid: document.getElementById("doctorid" + cellData.doctorid).value,
            name: document.getElementById("name" + cellData.doctorid).value,
            role: this.state.role,
            username: document.getElementById("doctorid" + cellData.doctorid).value,
        }
        this.props.callback(data, "save")
        this.setState({ editMode: false })
    }
    controlBar() {
        if (this.state.editMode) {
            return (
                <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", height: "100%" }}>
                    <Icon type="save" onClick={this.onSave} />
                    <Icon type="close" onClick={() => this.setState({ editMode: false })} />
                </div>
            )
        }
        return (
            <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", height: "100%" }}>
                <Icon type="edit" onClick={() => this.setState({ editMode: true })} />
                <Icon type="delete" onClick={this.showModal} />
            </div>
        )
    }
    deleteModule() {
        const { cellData, indexNum } = this.props
        return (
            <Modal
                title="刪除帳號"
                visible={this.state.deleteMode}
                onOk={this.handleOk}
                onCancel={this.handleCancel}>
                <div>確定刪除{cellData.doctorid}</div>
            </Modal>
        )
    }

    adduserhandleChange(value) {
        this.setState({ role: value })
    }
    render() {
        function formatRole(role) {
            if (role === 0) {
                return "唯讀使用者"
            }
            if (role === 1) {
                return "一般使用者"
            }
            if (role === 2) {
                return "系統設定管理員"
            }
        }
        function formatRoleContext(role) {
            if (role === 0) {
                return "僅可瀏覽病房總覽、歷史資料查詢"
            }
            if (role === 1) {
                return "可瀏覽和編輯病房總覽、歷史資料查詢"
            }
            if (role === 2) {
                return "可瀏覽和編輯權限設定、系統摘要"
            }
        }
        const { cellData } = this.props
        const { Option } = Select;
        //編號 姓名 醫生帳號 角色 權限 控制
        if (this.state.editMode) {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "2fr 3fr 2fr 6fr 1fr", height: "50px", lineHeight: "50px" }}>
                    <div style={{ display: "flex", alignItems: "center", padding: "0px 5px" }}>
                        <Input id={"name" + cellData.doctorid} defaultValue={cellData.name}></Input>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", padding: "0px 5px" }}>
                        <Input id={"doctorid" + cellData.doctorid} defaultValue={cellData.doctorid}></Input>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", padding: "0px 5px" }}>
                        <Select defaultValue={this.state.role} style={{ width: 150 }} onChange={this.adduserhandleChange}>
                            <Option value={0}>唯讀使用者</Option>
                            <Option value={1}>一般使用者</Option>
                            <Option value={2}>系統設定管理員</Option>
                        </Select>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", padding: "0px 5px", fontSize: "16px" }}>{formatRoleContext(cellData.role)}</div>
                    <div style={{ padding: "0px 5px" }}>{this.controlBar()}</div>
                    {this.deleteModule()}
                </div>
            );
        }
        return (
            <div style={{ display: "grid", gridTemplateColumns: "2fr 3fr 2fr 6fr 1fr", height: "50px", lineHeight: "50px" }}>
                <div style={{ display: "flex", alignItems: "center", padding: "0px 5px", fontSize: "16px" }}>{cellData.name}</div>
                <div style={{ display: "flex", alignItems: "center", padding: "0px 5px", fontSize: "16px" }}>{cellData.doctorid}</div>
                <div style={{ display: "flex", alignItems: "center", padding: "0px 5px", fontSize: "16px" }}>{formatRole(cellData.role)}</div>
                <div style={{ display: "flex", alignItems: "center", padding: "0px 5px", fontSize: "16px" }}>{formatRoleContext(cellData.role)}</div>
                <div style={{ padding: "0px 5px" }}>{this.controlBar()}</div>
                {this.deleteModule()}
            </div>
        );
    }
}